<template>
  <div class='department-box'>
    <van-tabs v-model="active" animated>
      <van-tab title="选择科室">
        <div class="select-box">
          <form action="/">
            <van-search
              v-model="selectDoctorVal"
              placeholder="搜索科室或医生"
              show-action
              @search="onSearch"
              @cancel="onCancel"
            />
          </form>
        </div>
         <van-tree-select
            :items="items"
            :active-id.sync="activeId"
            :main-active-index.sync="activeIndex"
            @click-item='clickChildFn'
          />
      </van-tab>
      <van-tab title="历史就诊医生">
        <div class="select-box">
          <form action="/">
            <van-search
              v-model="selectDoctorVal"
              placeholder="搜索科室或医生"
              show-action
              @search="onSearch"
              @cancel="onCancel"
            />
          </form>
        </div>
        <div class="doctor-item">
          <van-notice-bar mode="closeable" class="mb-5"> 温馨提示：只能查询最近的10个就诊医生</van-notice-bar>
          <div v-for="(item,index) in 10" class="mb-5 ml-5 mr-5" :key="index">
            <van-card
              desc="职位描述"  
              title="医生名字"
              thumb="https://img.yzcdn.cn/vant/t-thirt.jpg"
            >
              <div slot="footer">
                <van-button @click="goDoctorspaceFn">预约</van-button>
              </div>
            </van-card>
          </div>
            
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
let item=[
  {
    // 导航名称
    text: '儿科',
    // 导航节点额外类名
    className: 'my-class',
    // 该导航下所有的可选项
    children: [
      {
        text: '儿科口鼻',
        id: 1,
       // disabled: true  //禁用选项 
      },
      {
        text: '儿科发烧',
        id: 2
      }
    ]
  }
]
  export default {
    name: '',
    data(){
      return{
        active:0,
        items:item,
        activeId: 1,
        activeIndex: 0,
        selectDoctorVal:''
      }
    },
    methods:{
      //科室选中发生事件
      clickChildFn(data){     
        console.log('data: ', data);
        this.$router.push({path:'/selectDoctor',query:{setid:123456}})
      },
      onSearch(){ //搜索医生或者科室

      },
      onCancel(){   //清空输入框
        this.selectDoctorVal = ''
      },
      goDoctorspaceFn(){
        this.$router.push({path:'/doctorScheduling',query:{setid:123456}})

      }
    }
  };
</script>
<style lang='scss' scoped>
.department-box{
  width: 100%;
  .select-box{margin: 5px 0;}
  .van-card{
    position: relative;
    background: #fff;
    .van-card__title{
      font-size: 16px;
      margin-bottom: 5px;
    }
    .van-card__footer{
      position: absolute;
      top: 10px;
      right: 10px;  
    }
  }
}
</style>


